.logo{
    font-family: 'New Tegomin', serif;
    font-size: calc(1rem + 1vw);
    font-weight: bold;
    padding: 0;
    margin: 1vw;
    vertical-align: super;
    -webkit-text-decoration: 0;
            text-decoration: 0;
    color: black;

}
.bold{
    font-weight: bold;
}
nav .menu a {
    -webkit-text-decoration: 0;
            text-decoration: 0;
    text-transform: uppercase;
    font-weight: lighter;
    letter-spacing: -.5px;
    padding:.25vw;
    margin: 0 1vw;
    font-size: 1rem;    
    color: #333;
    position: relative;

}
nav .menu a:hover {
    color: black;
}
.menu a::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    border-bottom: 1px solid transparent;
    overflow: hidden;
}
.menu a::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
    border-bottom: 1px solid transparent;
}
.menu a:hover::before {
    -webkit-animation: border-btm .2s ease-in-out ;
            animation: border-btm .2s ease-in-out ;
    border-top: 1px solid rgb(109, 109, 109);
}
.menu a:hover::after {
    -webkit-animation: border-top .2s ease-in-out ;
            animation: border-top .2s ease-in-out ;
    border-bottom: 1px solid rgb(109, 109, 109);
}
@-webkit-keyframes border-btm{
    0%{
        border-bottom: 1px solid transparent;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
    100%{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}
@keyframes border-btm{
    0%{
        border-bottom: 1px solid transparent;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
    100%{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}
@-webkit-keyframes border-top{
    0%{
        border-bottom: 1px solid transparent;
        -webkit-transform: translateX(50%);
                transform: translateX(50%);
    }
    100%{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}
@keyframes border-top{
    0%{
        border-bottom: 1px solid transparent;
        -webkit-transform: translateX(50%);
                transform: translateX(50%);
    }
    100%{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

